import React from 'react';
import PropTypes from 'prop-types';
import './skl-ds-divider.css';

export const Divider = ({ color, thickness,}) => {
  const styles = {
    backgroundColor: color,
    height: thickness 
  };

  return <div class="skl-ds-divider" style={styles} role="separator" />;
};

Divider.propTypes = {
  color: PropTypes.string,
  thickness: PropTypes.string,
};

Divider.defaultProps = {
  color: '#ccc',
  thickness: '1px',
};
